/* eslint-disable */

export const displayMap = (locations) => {};

mapboxgl.accessToken =
  "pk.eyJ1IjoiYWFuZG0tdHJhdmVsbGVycyIsImEiOiJjbHEzOHh5dWQwYWFuMmttc2tncjJ3M2c1In0.bQMJZ4IOcYG9mB8lxXGNKw";

var map = new mapboxgl.Map({
  container: "map",
  //   style: "mapbox://styles/mapbox/streets-v11",
  style: "mapbox://styles/aandm-travellers/clq3atd2r00ns01ol763bct9s",
  scrollZoom: false,
});

const bounds = new mapboxgl.LngLatBounds();

locations.forEach((loc) => {
  // Create marker
  const el = document.createElement("div");
  el.className = "marker";

  // Add marker
  new mapboxgl.Marker({
    element: el,
    anchor: "bottom",
  })
    .setLngLat(loc.coordinates)
    .addTo(map);

  // Add popup
  new mapboxgl.Popup({
    offset: 30,
  })
    .setLngLat(loc.coordinates)
    .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
    .addTo(map);

  // Extend the map bounds to include the current location
  bounds.extend(loc.coordinates);
});

map.fitBounds(bounds, {
  padding: {
    top: 200,
    bottom: 150,
    left: 100,
    right: 100,
  },
});
